//以下是針對 圖片的 hover 顯示效果
.tagHover {
    position: relative;
    $tagBgColor: #494846;
    
    &:before, &:after {
        bottom: 100%;
        left: 10%;
        transform: translate(-50%, 0);
        position: absolute;
        z-index: 10;
        transition: all 0.18s ease-out 0.18s;
        opacity: 0;
    }
    
    // body of the tagBgcolor
&:after {
    background-color: $tagBgColor;
    border-radius: 4px;
    color: #F8DE77;
    content: attr(tags-data);
    margin-bottom: 10px;
    padding: 5px 11px;
}

    // tip (triangle) of the tagHover
&:before {
    content: "";
    margin-bottom: 4px;
    border-top: solid 8px $tagBgColor;
    border-left: solid 8px transparent;
    border-right: solid 8px transparent;
}

    &:hover:before,
    &:hover:after {
        opacity: 1;
    }
}

